.Flipper {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #fff;
  background: linear-gradient(
    to right bottom,
    #00b8ff 0%,
    #001eff 20%,
    #d600ff 100%
  );
}

.Flipper-title {
  font-weight: lighter;
  font-size: 42px;
}

.Coin-Container {
  margin-bottom: 25px;
}

.Flipper-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  font-size: 16px;
  background: linear-gradient(to right, #9102c5, #d600ff);

  background-size: 300px;
  transition: background 0.3s ease-in;
}

.Flipper-button:hover {
  background-position: 100px;
}

.Flipper-button:focus {
  outline: none;
}

.Flipper-count {
  font-size: 24px;
  margin: 20px 0;
}

@media only screen and (max-width: 400px) {
  .Flipper-title {
    font-size: 32px;
  }

  .Flipper-count {
    font-size: 18px;
    width: 80vw;
  }

  .Flipper-button {
    font-size: 14px;
  }
}
