.Coin {
  max-width: 200px;
  margin: 0 auto;
  position: relative;
}

.Coin img {
  width: 100%;
  display: block;
  clip-path: circle(48% at 50% 50%);
}

.Coin.loading img {
  animation: rotate 0.5s linear infinite;
}

.Coin::before {
  content: '';
  display: block;
  background: #bd00ff;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 50%;
  mix-blend-mode: overlay;
  z-index: 10;
}

.Coin.loading::before {
  animation: rotate 0.5s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate3d(0, 1, 0, 360deg) perspective(1000px);
  }

  100% {
    transform: rotate3d(0, 0, 0, 0deg) perspective(10px);
  }
}

@media only screen and (max-width: 400px) {
  .Coin {
    max-width: 150px;
  }
}
