html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Coin {
  max-width: 200px;
  margin: 0 auto;
  position: relative;
}

.Coin img {
  width: 100%;
  display: block;
  -webkit-clip-path: circle(48% at 50% 50%);
          clip-path: circle(48% at 50% 50%);
}

.Coin.loading img {
  -webkit-animation: rotate 0.5s linear infinite;
          animation: rotate 0.5s linear infinite;
}

.Coin::before {
  content: '';
  display: block;
  background: #bd00ff;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 50%;
  mix-blend-mode: overlay;
  z-index: 10;
}

.Coin.loading::before {
  -webkit-animation: rotate 0.5s linear infinite;
          animation: rotate 0.5s linear infinite;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate3d(0, 1, 0, 360deg) perspective(1000px);
            transform: rotate3d(0, 1, 0, 360deg) perspective(1000px);
  }

  100% {
    -webkit-transform: rotate3d(0, 0, 0, 0deg) perspective(10px);
            transform: rotate3d(0, 0, 0, 0deg) perspective(10px);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate3d(0, 1, 0, 360deg) perspective(1000px);
            transform: rotate3d(0, 1, 0, 360deg) perspective(1000px);
  }

  100% {
    -webkit-transform: rotate3d(0, 0, 0, 0deg) perspective(10px);
            transform: rotate3d(0, 0, 0, 0deg) perspective(10px);
  }
}

@media only screen and (max-width: 400px) {
  .Coin {
    max-width: 150px;
  }
}

.Flipper {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #fff;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    from(#00b8ff),
    color-stop(20%, #001eff),
    to(#d600ff)
  );
  background: linear-gradient(
    to right bottom,
    #00b8ff 0%,
    #001eff 20%,
    #d600ff 100%
  );
}

.Flipper-title {
  font-weight: lighter;
  font-size: 42px;
}

.Coin-Container {
  margin-bottom: 25px;
}

.Flipper-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  font-size: 16px;
  background: -webkit-gradient(linear, left top, right top, from(#9102c5), to(#d600ff));
  background: linear-gradient(to right, #9102c5, #d600ff);

  background-size: 300px;
  -webkit-transition: background 0.3s ease-in;
  transition: background 0.3s ease-in;
}

.Flipper-button:hover {
  background-position: 100px;
}

.Flipper-button:focus {
  outline: none;
}

.Flipper-count {
  font-size: 24px;
  margin: 20px 0;
}

@media only screen and (max-width: 400px) {
  .Flipper-title {
    font-size: 32px;
  }

  .Flipper-count {
    font-size: 18px;
    width: 80vw;
  }

  .Flipper-button {
    font-size: 14px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

